<template>
  <b-card :header="caption">
    <b-table :hover="hover" :striped="striped" :bordered="bordered" :small="small" :fixed="fixed" responsive="sm" :items="graphruns" :fields="graphruns_fields" :current-page="currentPage" :per-page="perPage">
      <template slot="status" slot-scope="data">
        <b-badge :variant="getBadge(data.item.status)">{{data.item.status}}</b-badge>
      </template>
    </b-table>
    <nav>
      <b-pagination :total-rows="getRowCount(items)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
    </nav>
  </b-card>
</template>

<script>
  /**
   * Randomize array element order in-place.
   * Using Durstenfeld shuffle algorithm.
   */
//  const shuffleArray = (array) => {
//    for (let i = array.length - 1; i > 0; i--) {
//      let j = Math.floor(Math.random() * (i + 1))
//      let temp = array[i]
//      array[i] = array[j]
//      array[j] = temp
//    }
//    return array
//  }
  import { mapGetters, mapActions } from 'vuex'

  export default {
    name: 'c-table',
    props: {
      caption: {
        type: String,
        default: 'Table'
      },
      hover: {
        type: Boolean,
        default: false
      },
      striped: {
        type: Boolean,
        default: false
      },
      bordered: {
        type: Boolean,
        default: false
      },
      small: {
        type: Boolean,
        default: false
      },
      fixed: {
        type: Boolean,
        default: false
      }
    },
    data: () => {
      return {
        items: [
          {username: 'Samppa Nori', registered: '2012/01/01', role: 'Member', status: 'Active'},
          {username: 'Estavan Lykos', registered: '2012/02/01', role: 'Staff', status: 'Banned'},
          {username: 'Chetan Mohamed', registered: '2012/02/01', role: 'Admin', status: 'Inactive'},
          {username: 'Derick Maximinus', registered: '2012/03/01', role: 'Member', status: 'Pending'},
          {username: 'Friderik Dvid', registered: '2012/01/21', role: 'Staff', status: 'Active'},
          {username: 'Yiorgos Avraamu', registered: '2012/01/01', role: 'Member', status: 'Active'},
          {username: 'Avram Tarasios', registered: '2012/02/01', role: 'Staff', status: 'Banned'},
          {username: 'Quintin Ed', registered: '2012/02/01', role: 'Admin', status: 'Inactive'},
          {username: 'Enas Kwadwo', registered: '2012/03/01', role: 'Member', status: 'Pending'},
          {username: 'Agapetus Tade', registered: '2012/01/21', role: 'Staff', status: 'Active'},
          {username: 'Carwyn Fachtna', registered: '2012/01/01', role: 'Member', status: 'Active'},
          {username: 'Nehemiah Tatius', registered: '2012/02/01', role: 'Staff', status: 'Banned'},
          {username: 'Ebbe Gemariah', registered: '2012/02/01', role: 'Admin', status: 'Inactive'},
          {username: 'Eustorgios Amulius', registered: '2012/03/01', role: 'Member', status: 'Pending'},
          {username: 'Leopold Gspr', registered: '2012/01/21', role: 'Staff', status: 'Active'},
          {username: 'Pompeius Ren', registered: '2012/01/01', role: 'Member', status: 'Active'},
          {username: 'Pajo Jadon', registered: '2012/02/01', role: 'Staff', status: 'Banned'},
          {username: 'Micheal Mercurius', registered: '2012/02/01', role: 'Admin', status: 'Inactive'},
          {username: 'Ganesha Dubhghall', registered: '2012/03/01', role: 'Member', status: 'Pending'},
          {username: 'Hiroto imun', registered: '2012/01/21', role: 'Staff', status: 'Active'},
          {username: 'Vishnu Serghei', registered: '2012/01/01', role: 'Member', status: 'Active'},
          {username: 'Zbynk Phoibos', registered: '2012/02/01', role: 'Staff', status: 'Banned'},
          {username: 'Einar Randall', registered: '2012/02/01', role: 'Admin', status: 'Inactive'},
          {username: 'Flix Troels', registered: '2012/03/21', role: 'Staff', status: 'Active'},
          {username: 'Aulus Agmundr', registered: '2012/01/01', role: 'Member', status: 'Pending'}
        ],
        fields: [
          {key: 'username'},
          {key: 'registered'},
          {key: 'role'},
          {key: 'status'}
        ],
        graphruns_fields: [
          {key: 'id'},
          {key: 'graph'},
          {key: 'start_time'},
          {key: 'status'},
          {key: 'actions'}
        ],
        currentPage: 1,
        perPage: 10,
        totalRows: 0
      }
    },
    computed: {
      ...mapGetters({
        graphruns: 'graph/graphruns'
      })
    },
    methods: {
      ...mapActions({
        setGraphRuns: 'graph/setGraphRuns'
      }),
      working (msg) {
        alert(msg)
      },
      getBadge (status) {
        return status === 'Started' ? 'secondary'
          : status === 'Success' ? 'success'
            : status === 'NA' ? 'warning'
              : status === 'Error' ? 'danger' : 'primary'
      },
      getRowCount (items) {
        return items.length
      }
    },
    mounted () {
      this.setGraphRuns()
    }
  }
</script>



